/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
// eslint-disable react-hooks/exhaustive-deps
import React, { useState, useEffect, useReducer } from 'react';
import * as AuthSelectors from 'store/auth/shared/selectors';
import * as AuthSlice from 'store/auth/shared/slice';
import * as _ from 'lodash';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { ModalHRM } from '../components/ModalHRM';
import { AuthSaga } from 'store/auth/shared/saga';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from '@material-ui/core';
import {
  ButtonToolTipPersonnel,
  DialogKeyPersonnel,
  DialogTitlePersonnel,
  PersonnelStatus,
} from 'store/auth/constants/auth.constant';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RESPONSE_MESSAGE } from 'store/auth/constants/auth.constant';
import { SettingHttp } from 'store/setting/services/setting.http';
import { SettingManageType } from 'store/setting/constants/setting.constant';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { useForm } from 'react-hook-form';
import Switch from '@material-ui/core/Switch';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import Tooltip from '@material-ui/core/Tooltip';
import 'react-datepicker/dist/react-datepicker.css';

export const colsHRM = [
  { id: '#', lable: 'No', minWidth: 100 },
  { id: 'actions', lable: 'Chc nng', minWidth: 200 },
  { id: 'is_active', lable: 'Trng thi', minWidth: 100 },
  { id: 'user_name', lable: 'Tn ng nhp', minWidth: 200 },
  { id: 'code', lable: 'M nhn vin', minWidth: 200 },
  { id: 'full_name', lable: 'Tn nhn vin', minWidth: 250 },
  { id: 'phone', lable: 'S in thoi', minWidth: 200 },
  { id: 'sex', lable: 'Gii tnh', minWidth: 100 },
  { id: 'birthdate', lable: 'Ngy sinh', minWidth: 120 },
  { id: 'title_description', lable: 'Chc danh', minWidth: 300 },
  { id: 'position_description', lable: 'Chc v', minWidth: 300 },
  { id: 'data_type', lable: 'Loi d liu', minWidth: 100 },
];

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
    root: {
      width: '100%',
    },
    container: {
      minHeight: 400,
    },
    td: {
      fontSize: '1.1rem',
      fontFamily: 'Roboto, sans-serif',
    },
  }),
);
export interface AlertConfig {
  isOpen: boolean;
  autoHide: number;
}

const defaultPagi = {
  pageNumber: 1,
  pageSize: 10,
};

export function HRM() {
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const settingHttp = new SettingHttp();
  const classes = useStyles();
  const dateUtil = new DateFnsUtils();
  useInjectReducer({ key: AuthSlice.sliceKey, reducer: AuthSlice.reducer });
  useInjectSaga({ key: AuthSlice.sliceKey, saga: AuthSaga });
  const users = useSelector(AuthSelectors.selectUsers);
  const loading = useSelector(AuthSelectors.selectLoading);
  const error = useSelector(AuthSelectors.selectError);
  const dispatch = useDispatch();
  const { handleSubmit, register, getValues, reset } = useForm();
  const [isConfirmDelete, setIsConfirmDelete] = useState(false);
  const [isConfirmChangeStatus, setIsConfirmChangeStatus] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [pagi, setPagi] = useState(defaultPagi);
  const [selectPersonnel, setSelectPersonnel] = useState({});
  const [selectIsActive, setSelectIsActive] = useState({});

  useEffect(() => {
    async function getTitlePostion() {
      try {
        const response: Promise<any>[] = await Promise.all([
          settingHttp.getAllManageType(SettingManageType.Title),
          settingHttp.getAllManageType(SettingManageType.Position),
        ]);
        const [
          titles,
          positions,
        ]: any[] = await response.map(({ data: { result } }: any) =>
          _.get(result, 'data'),
        );
        if (titles.length && positions.length) {
          dispatch(AuthSlice.actions.setUserTitles(titles));
          dispatch(AuthSlice.actions.setUserPositions(positions));
        }
      } catch (err) {
        notifyController
          .setNotifyMessage(_.get(error, 'message'))
          .setFailNotify();
      }
    }
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case AuthSlice.actions.getUserByIdSuccess.type:
          setOpenModal(true);
          break;
        case AuthSlice.actions.createPersonnelSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CREATE_PERSONNEL_SUCCESS)
            .setSuccessNotify();
          setOpenModal(false);
          recallListUser(pagi);
          break;
        case AuthSlice.actions.updatePersonnelSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.UPDATE_PERSONNEL_SUCCESS)
            .setSuccessNotify();
          setOpenModal(false);
          recallListUser(pagi);
          break;
        case AuthSlice.actions.changeActivePersonnelSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CHANGE_ACTIVE_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.deletePersonnelSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.DELETE_MESSAGE_SUCCESS)
            .setSuccessNotify();
          recallListUser(pagi);
          break;
        case AuthSlice.actions.deletePersonnelFail.type:
          notifyController
            .setNotifyMessage(
              payload ? payload : RESPONSE_MESSAGE.DELETE_MESSAGE_FAIL,
            )
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    getTitlePostion();
    dispatch(AuthSlice.actions.getListUser(pagi));
    return () => {
      storeSub$();
    };
  }, []);

  const recallListUser = (newPagi: any) => {
    setPagi(newPagi);
    dispatch(
      AuthSlice.actions.getListUser({
        ...newPagi,
        fullName: getValues('fullName') ? getValues('fullName') : '',
      }),
    );
  };

  const handleDialogControl = (key, personnel?, isActive?) => {
    setSelectPersonnel(personnel);
    setSelectIsActive(isActive);
    switch (key) {
      case DialogKeyPersonnel.DELETE_PERSONNEL:
        setIsConfirmDelete(true);
        break;
      case DialogKeyPersonnel.CHANGE_STATUS_PERSONNEL:
        setIsConfirmChangeStatus(true);
        break;
      case DialogKeyPersonnel.CREATE_PERSONNEL:
        setOpenModal(true);
        break;
      default:
        break;
    }
  };

  const closeDialogControl = key => {
    switch (key) {
      case DialogKeyPersonnel.DELETE_PERSONNEL:
        setIsConfirmDelete(false);
        break;
      case DialogKeyPersonnel.CHANGE_STATUS_PERSONNEL:
        setIsConfirmChangeStatus(false);
        break;
      case DialogKeyPersonnel.CREATE_PERSONNEL:
        setOpenModal(false);
        break;
      default:
        break;
    }
  };

  const renderColumns = () => (
    <TableRow>
      {colsHRM.map(column => (
        <TableCell
          key={column.id}
          style={{
            minWidth: column.minWidth,
            background: '#1a8ae2',
            color: '#fff',
          }}
        >
          {column.lable}
        </TableCell>
      ))}
    </TableRow>
  );

  const renderTable = () => {
    if (_.isEmpty(users) || !users?.data?.length)
      return (
        <TableRow>
          <TableCell className="w-100" align="center" colSpan={colsHRM.length}>
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    return users.data.map((personnel, _id) => (
      <TableRow key={personnel.id} hover>
        {colsHRM.map(column => {
          const value = personnel[column.id];
          if (column.id === '#') {
            const { page_number, page_size } = users;
            return (
              <TableCell key={column.id} className={classes.td}>
                {page_number === 1
                  ? page_number + _id
                  : _id + 1 + (page_number - 1) * page_size}
              </TableCell>
            );
          }
          if (column.id === 'birthdate') {
            return (
              <TableCell key={column.id} className={classes.td}>
                {value
                  ? dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE)
                  : ''}
              </TableCell>
            );
          }
          if (column.id === 'image_url') {
            return (
              <TableCell key={column.id} className={classes.td}>
                {value ? (
                  value
                ) : (
                  <i className="fa fa-users" aria-hidden="true"></i>
                )}
              </TableCell>
            );
          }
          if (column.id === 'sex' && !_.isNull(value)) {
            return (
              <TableCell key={column.id} className={classes.td}>
                {value && +value ? 'Nam' : 'N'}
              </TableCell>
            );
          }
          if (column.id === 'is_active') {
            return (
              <TableCell key={column.id} className={classes.td}>
                <Tooltip title="" enterDelay={500} leaveDelay={200}>
                  <i
                    style={{
                      color: value ? '#4caf50' : '#ff0000',
                      fontSize: '16px',
                    }}
                    className={`fa fa-${value ? 'check' : 'times'}-circle`}
                    aria-hidden="true"
                  ></i>
                </Tooltip>
              </TableCell>
            );
          }
          if (column.id === 'actions') {
            return (
              <TableCell key={column.id} className={classes.td}>
                <Tooltip
                  title={ButtonToolTipPersonnel.CHANGE_STATUS}
                  enterDelay={500}
                  leaveDelay={100}
                >
                  <Switch
                    color="primary"
                    checked={Boolean(personnel.is_active)}
                    onChange={({
                      target: { checked },
                    }: React.ChangeEvent<HTMLInputElement>) => {
                      dispatch(
                        AuthSlice.actions.changeActivePersonnel({
                          id: personnel.id,
                          isActive: checked,
                        }),
                      );
                    }}
                    name="IsActive"
                  />
                </Tooltip>
                <Tooltip
                  title={ButtonToolTipPersonnel.EDIT}
                  enterDelay={500}
                  leaveDelay={100}
                >
                  <button
                    className="btn btn-primary btn-xs"
                    onClick={() => {
                      dispatch(AuthSlice.actions.getUserById(personnel.id));
                    }}
                  >
                    <i className="fa fa-pencil"></i>
                  </button>
                </Tooltip>
                <Tooltip
                  title={ButtonToolTipPersonnel.DELETE}
                  enterDelay={500}
                  leaveDelay={100}
                >
                  <button
                    className="btn btn-danger btn-xs mx-2"
                    onClick={() =>
                      handleDialogControl(
                        DialogKeyPersonnel.DELETE_PERSONNEL,
                        personnel.id,
                      )
                    }
                  >
                    <i className="fa fa-trash-o"></i>
                  </button>
                </Tooltip>
              </TableCell>
            );
          }
          return (
            <TableCell key={column.id} className={classes.td}>
              {value ? value : '-'}
            </TableCell>
          );
        })}
      </TableRow>
    ));
  };

  const renderTableControl = () => {
    return (
      <form onSubmit={handleSubmit(() => recallListUser(defaultPagi))}>
        <div className="row">
          <div className="col-md-4 form-group">
            <label>Tm tn nhn s</label>
            <input
              className="form-control mr-4"
              ref={register()}
              placeholder="Tm tn nhn s"
              type="text"
              name="fullName"
            />
          </div>
          <div className="col-md-4 form-group">
            <div className="btn-group">
              <button
                className="btn btn-primary mr-3 btn__search"
                type="submit"
              >
                Tm kim <i className="fa fa-search"></i>
              </button>
            </div>
          </div>
          <div className="col-md-4 form-group export__examination text-right">
            <div className="btn-group ml-3">
              <button
                type="reset"
                className="btn btn-warning btn__export"
                onClick={async () => {
                  await reset();
                  recallListUser(defaultPagi);
                }}
              >
                Reset <i className="fa fa-refresh"></i>
              </button>
            </div>
            <div className="btn-group ml-3">
              <button
                className="btn btn-success mr-3 edit__add"
                type="button"
                onClick={() =>
                  handleDialogControl(DialogKeyPersonnel.CREATE_PERSONNEL)
                }
              >
                Thm mi <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const renderPagination = () => {
    if (!_.isEmpty(users) && users.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin th"
          count={users.total_records}
          page={users.page_number - 1}
          rowsPerPage={users.page_size}
          onChangePage={(event: unknown, pageNumber: number) => {
            let newPagi = Object.assign({}, pagi, {
              pageNumber: pageNumber + 1,
            });
            recallListUser(newPagi);
          }}
          onChangeRowsPerPage={event => {
            let newPagi = Object.assign({}, pagi, {
              pageSize: +event.target.value,
              pageNumber: 0,
            });
            recallListUser(newPagi);
          }}
        />
      );
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">
          <ul className="nav customtab nav-tabs" role="tablist"></ul>
          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-topline-red">
                    <div className="card-head">
                      <header></header>
                      <div className="tools"></div>
                    </div>
                    <div className="card-body ">
                      {renderTableControl()}
                      <Paper className={classes.root}>
                        {loading ? <AppLoading loading={true} /> : null}
                        <TableContainer className={classes.container}>
                          <Table stickyHeader aria-label="sticky table">
                            <TableHead>{renderColumns()}</TableHead>
                            <TableBody>{renderTable()}</TableBody>
                          </Table>
                        </TableContainer>
                        {renderPagination()}
                      </Paper>
                      <ModalConfirm
                        title={DialogTitlePersonnel.DELETE_TITLE_PERSONNEL}
                        keyConfirm={DialogKeyPersonnel.DELETE_PERSONNEL}
                        open={isConfirmDelete}
                        onClose={() =>
                          closeDialogControl(
                            DialogKeyPersonnel.DELETE_PERSONNEL,
                          )
                        }
                        confirmMethod={() =>
                          dispatch(
                            AuthSlice.actions.deletePersonnel(selectPersonnel),
                          )
                        }
                      ></ModalConfirm>
                      {openModal ? (
                        <ModalHRM
                          open={openModal}
                          onClose={() =>
                            closeDialogControl(
                              DialogKeyPersonnel.CREATE_PERSONNEL,
                            )
                          }
                        />
                      ) : null}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
