/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useMemo, useReducer } from 'react';
import '../role.css';
import * as _ from 'lodash';
import * as AuthSlice from 'store/auth/shared/slice';
import * as AuthSelector from 'store/auth/shared/selectors';
import { AuthSaga } from 'store/auth/shared/saga';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { ModalUserInRole } from '../components/ModalUserInRole';
import { ModalAddRole } from '../components/ModalAddRole';
import { ConfirmDeleteRole } from '../components/ConfirmDeleteRole';
import { useForm } from 'react-hook-form';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RESPONSE_MESSAGE } from 'store/auth/constants/auth.constant';
import { rowsPerArray } from 'store/common/constants/common.constant';
import AppLoading from 'app/components/Loading';
import TablePagination from '@material-ui/core/TablePagination';
import { Table, Space, Button } from 'antd';
import {
  CheckSquareTwoTone,
  DeleteOutlined,
  InfoOutlined,
} from '@ant-design/icons';
import 'antd/dist/antd.css';
import { Link } from 'react-router-dom';

interface ModalState {
  isAdd: boolean;
  isDelete: boolean;
  idDelete: string;
  isUser: boolean;
  isPermission: boolean;
}

enum ModalType {
  ADD = 'ADD',
  DELETE = 'DELETE',
  USER = 'USER',
  PERMISSION = 'PERMISSION',
}

type ModalAction = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  isAdd: false,
  isDelete: false,
  idDelete: '',
  isUser: false,
  isPermission: false,
};

const reduceModalState = (
  state: ModalState = initModalState,
  action: ModalAction,
): ModalState => {
  const { type, payload } = action;
  switch (type) {
    case ModalType.ADD:
      return {
        ...state,
        isAdd: payload,
      };
    case ModalType.PERMISSION:
      return {
        ...state,
        isPermission: payload,
        isUser: false,
      };
    case ModalType.USER:
      return {
        ...state,
        isUser: payload,
      };
    case ModalType.DELETE:
      const { id, isDelete } = payload;
      return {
        ...state,
        idDelete: id,
        isDelete: isDelete,
      };
    default:
      return state;
  }
};

export const colsRole: any[] = [
  { id: 'no', label: '#', minWidth: 50 },
  { id: 'code', label: 'M', minWidth: 120 },
  { id: 'name', label: 'Tn', minWidth: 120 },
  { id: 'description', label: 'M t', minWidth: 120 },
  { id: 'is_active', label: 'Trng thi', minWidth: 120 },
  { id: 'actions', label: 'Chc nng', minWidth: 170 },
];

const ADD_USER_IN_ROLE_SUCCESS = 'Thm vo ngi dng vo role thnh cng';
const ADD_USER_IN_ROLE_FAIL = 'Thm vo ngi dng vo role tht bi';
const DELETE_USER_IN_ROLE_SUCCESS = 'Xo ngi dng khi role thnh cng';
const DELETE_USER_IN_ROLE_FAIL = 'Xo ngi dng khi role tht bi';
const UPDATE_PERMISSION_SUCCESS = 'Thay i quyn truy cp Thnh Cng';
const UPDATE_PERMISSION_FAIL = 'Thay i quyn truy cp tht bi';

const defaultPagi = {
  pageNumber: 1,
  pageSize: 10,
};

export function Role() {
  useInjectReducer({ key: AuthSlice.sliceKey, reducer: AuthSlice.reducer });
  useInjectSaga({ key: AuthSlice.sliceKey, saga: AuthSaga });
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const loading: any = useSelector<any>(AuthSelector.selectLoading);
  const roles: any = useSelector<any>(AuthSelector.selectRoles);
  const [modalState, dispatchModal] = useReducer(
    reduceModalState,
    initModalState,
  );
  const { handleSubmit, register, reset, getValues } = useForm({});
  const [pagi, setPagi] = useState(defaultPagi);

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case AuthSlice.actions.getRoleByIdSuccess.type:
          if (payload.type === ModalType.USER) {
            dispatchModal({
              type: ModalType.USER,
              payload: true,
            });
          }
          break;
        case AuthSlice.actions.addRoleSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.ADD_ROLE_SUCCESS)
            .setSuccessNotify();
          dispatch(AuthSlice.actions.getListRole(defaultPagi));
          dispatchModal({
            type: ModalType.ADD,
            payload: false,
          });
          break;
        case AuthSlice.actions.updatePermissionInRoleSuccess.type:
          notifyController
            .setNotifyMessage(UPDATE_PERMISSION_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.updatePermissionInRoleFail.type:
          notifyController
            .setNotifyMessage(UPDATE_PERMISSION_FAIL)
            .setFailNotify();
          break;
        case AuthSlice.actions.deleteRoleSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.DELETE_ROLE_SUCCESS)
            .setSuccessNotify();
          dispatch(AuthSlice.actions.getListRole(defaultPagi));
          break;
        case AuthSlice.actions.addRoleFail.type:
        case AuthSlice.actions.deleteRoleFail.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        case AuthSlice.actions.deleteUserInRoleSuccess.type:
          notifyController
            .setNotifyMessage(DELETE_USER_IN_ROLE_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.addUserInRoleSuccess.type:
          notifyController
            .setNotifyMessage(ADD_USER_IN_ROLE_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.addUserInRoleFail.type:
          notifyController
            .setNotifyMessage(payload ? payload : ADD_USER_IN_ROLE_FAIL)
            .setFailNotify();
          break;
        case AuthSlice.actions.deleteUserInRoleFail.type:
          notifyController
            .setNotifyMessage(payload ? payload : DELETE_USER_IN_ROLE_FAIL)
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const resetPatient = () => {
    setPagi(defaultPagi);
    dispatch(AuthSlice.actions.getListRole(defaultPagi));
    reset();
  };

  const handleChangePage = (event: unknown, pageNumber: number) => {
    let newPagi = {
      ...pagi,
      pageNumber: pageNumber + 1,
      search: getValues('search'),
    };
    setPagi(newPagi);
    dispatch(AuthSlice.actions.getListRole(newPagi));
  };

  const handleChangeRowsPerPage = event => {
    let newPagi = {
      pageSize: +event.target.value,
      pageNumber: 1,
      search: getValues('search'),
    };
    setPagi(newPagi);
    dispatch(AuthSlice.actions.getListRole(newPagi));
  };

  const renderTableControl = () => {
    return (
      <form
        onSubmit={handleSubmit(({ search }) => {
          const newPagi = {
            pageNumber: 1,
            pageSize: pagi.pageSize,
          };
          dispatch(
            AuthSlice.actions.getListRole({
              search,
              ...newPagi,
            }),
          );
          setPagi(newPagi);
        })}
      >
        <div className="row">
          <div className="col-md-4 form-group">
            <label>Tm quyn truy cp</label>
            <input
              className="form-control mr-4"
              ref={register()}
              placeholder="Tm quyn truy cp"
              type="text"
              name="search"
            />
          </div>
          <div className="col-md-4 form-group">
            <div className="btn-group">
              <button
                className="btn btn-primary mr-3 btn__search"
                type="submit"
              >
                Tm kim <i className="fa fa-search"></i>
              </button>
            </div>
          </div>
          <div className="col-md-4 form-group export__examination text-right">
            <div className="btn-group ml-3">
              <button
                type="button"
                className="btn btn-warning btn__export"
                onClick={resetPatient}
              >
                Reset <i className="fa fa-refresh"></i>
              </button>
            </div>
            <div className="btn-group ml-3">
              <button
                className="btn btn-success mr-3 edit__add"
                onClick={() =>
                  dispatchModal({
                    type: ModalType.ADD,
                    payload: true,
                  })
                }
                type="button"
              >
                Thm mi <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const renderData = useMemo(() => {
    const { data = [] } = roles;
    return {
      ...roles,
      data,
    };
  }, [roles]);

  // antd desgin
  const columnsAntD: any = [
    {
      title: 'M',
      dataIndex: 'code',
      key: 'code',
      width: '10%',
    },
    {
      title: 'Tn',
      dataIndex: 'name',
      key: 'name',
      width: '20%',
    },
    {
      title: 'M t',
      dataIndex: 'description',
      key: 'description',
      width: '40%',
    },
    {
      title: 'Trng thi',
      dataIndex: 'is_active',
      key: 'is_active',
      width: '10%',
      align: 'center',
      render: is_active => {
        return is_active ? (
          <CheckSquareTwoTone twoToneColor="#52c41a" />
        ) : (
          <CheckSquareTwoTone twoToneColor="#3a3f38" />
        );
      },
    },
    {
      title: 'Chc nng',
      dataIndex: 'chucnang',
      key: 'chucnang',
      width: '20%',
      align: 'center',
      render: (row, idx) => {
        const id = idx.id;
        return (
          <Space size="large">
            <Button
              type="primary"
              className="ant-btn btn-success btn-xs"
              onClick={() => {
                dispatchModal({
                  type: ModalType.PERMISSION,
                  payload: true,
                });
                dispatch(
                  AuthSlice.actions.getRoleById({
                    id,
                    type: ModalType.PERMISSION,
                  }),
                );
              }}
            >
              <Link to={{ pathname: '/role-detail', search: `?id=${id}` }}>
                <InfoOutlined />
              </Link>
            </Button>
            <Button
              type="primary"
              className="ant-btn btn-danger btn-xs mx-2"
              icon={<DeleteOutlined />}
              title="Xo quyn truy cp"
              onClick={() =>
                dispatchModal({
                  type: ModalType.DELETE,
                  payload: { isDelete: true, id },
                })
              }
            ></Button>
          </Space>
        );
      },
    },
  ];

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin Th"
          count={renderData.total_records}
          page={renderData?.page_number - 1}
          rowsPerPage={renderData?.page_size}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      {modalState.isAdd && (
        <ModalAddRole
          open={true}
          onClose={() =>
            dispatchModal({
              type: ModalType.ADD,
              payload: false,
            })
          }
        />
      )}
      {modalState.isUser && (
        <ModalUserInRole
          open={true}
          onClose={() =>
            dispatchModal({
              type: ModalType.USER,
              payload: false,
            })
          }
        />
      )}
      <ConfirmDeleteRole
        title={'Bn c chc chn mun xo quyn truy cp Ny?'}
        id={modalState.idDelete}
        open={modalState.isDelete}
        onClose={() =>
          dispatchModal({
            type: ModalType.DELETE,
            payload: { isDelete: false, id: '' },
          })
        }
        confirmMethod={id => dispatch(AuthSlice.actions.deleteRole(id))}
      />
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Table
                columns={columnsAntD}
                dataSource={renderData.data}
                pagination={false}
                rowKey={row => row.id}
                scroll={{ y: 780 }}
              />
              {renderPagination()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
