/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import { blue, green } from '@material-ui/core/colors';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Order } from 'utils/app.helper';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';

export const useStyles = makeStyles({
  th: {
    fontWeight: 600,
    textTransform: 'capitalize',
    backgound: 'rgb(26, 138, 226)',
    color: '#fff',
    '& .MuiTableSortLabel-active': {
      color: '#0000008a',
      '& .MuiTableSortLabel-icon': {
        color: '#0000008a !important',
      },
    },
    '& .MuiTableSortLabel-root:hover': {
      color: '#0000008a',
    },
  },
  root: {
    width: '100%',
  },
  container: {
    minHeight: '300px',
    maxHeight: '80vh',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export interface TableSortCheckProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof any,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order;
  orderBy: string;
  rowCount: number;
  cols: any[];
}

export const BlueCheckbox = withStyles({
  root: {
    color: blue[400],
    '&$checked': {
      color: blue[600],
    },
  },
  checked: {},
})((props: CheckboxProps) => <Checkbox color="default" {...props} />);

export const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600],
    },
  },
  checked: {},
})((props: CheckboxProps) => <Checkbox color="default" {...props} />);

export default function TableSortCheck(props: TableSortCheckProps) {
  const classes = useStyles();
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
    cols,
  } = props;
  const createSortHandler = (property: keyof any) => (
    event: React.MouseEvent<unknown>,
  ) => {
    onRequestSort(event, property);
  };

  return (
    <TableRow>
      <TableCell
        padding="checkbox"
        className={classes.th}
        style={{
          backgroundColor: 'rgb(26, 138, 226)',
          color: '#fff',
          padding: '16px',
        }}
      >
        <GreenCheckbox
          indeterminate={numSelected > 0 && numSelected < rowCount}
          checked={rowCount > 0 && numSelected === rowCount}
          onChange={onSelectAllClick}
          inputProps={{ 'aria-label': 'select all desserts' }}
        />
      </TableCell>
      {cols.map(headCell => (
        <TableCell
          className={classes.th}
          key={headCell.id}
          align={headCell.numeric ? 'right' : 'left'}
          padding={headCell.disablePadding ? 'none' : 'default'}
          sortDirection={orderBy === headCell.id ? order : false}
          style={{
            minWidth: headCell.minWidth,
            backgroundColor: '#0092de',
            color: '#fff',
            padding: '16px',
          }}
        >
          <TableSortLabel
            active={orderBy === headCell.id}
            direction={orderBy === headCell.id ? order : 'asc'}
            onClick={createSortHandler(headCell.id)}
          >
            {headCell.label}
            {orderBy === headCell.id ? (
              <span className={classes.visuallyHidden}>
                {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
              </span>
            ) : null}
          </TableSortLabel>
        </TableCell>
      ))}
    </TableRow>
  );
}
