/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect, useRef } from 'react';
import * as _ from 'lodash';
import * as AuthSlice from 'store/auth/shared/slice';
import * as AuthSelector from 'store/auth/shared/selectors';
import { useInjectReducer, useInjectSaga } from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { AuthSaga } from 'store/auth/shared/saga';
import { AppHelper, Order } from 'utils/app.helper';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';
import { EMPTY_MESSAGE } from 'store/common/constants/common.constant';
import { ModalFindUser } from './ModalFindUser';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableHeaderSort from 'app/components/TableHeaderSort';

export const colUserInRole: any[] = [
  { id: 'no', label: '#', minWidth: 50 },
  { id: 'user_name', label: 'Tn ng nhp', minWidth: 120 },
  { id: 'code', label: 'M nhn vin', minWidth: 120 },
  { id: 'name', label: 'Tn nhn vin', minWidth: 120 },
  { id: 'is_active', label: 'Trng thi', minWidth: 120 },
  { id: 'actions', label: 'Xo ngi dng', minWidth: 200 },
];

export const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    minHeight: '300px',
    maxHeight: '80vh',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export function ModalUserInRole({ open, onClose }) {
  useInjectReducer({ key: AuthSlice.sliceKey, reducer: AuthSlice.reducer });
  useInjectSaga({ key: AuthSlice.sliceKey, saga: AuthSaga });
  const dispatch = useDispatch();
  const classes = useStyles();
  const role = useSelector(AuthSelector.selectRoleEditing);
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('');
  const [search, setSearch] = useState<boolean>(false);

  const deleteUserInRole = (id: string) => {
    const activeUsers: string[] = role.users.filter(user => user.id !== id).map(({ id }) => id);
    dispatch( AuthSlice.actions.deleteUserInRole({
      id: role.id,
      activeUser: activeUsers.length ? activeUsers : [],
      inActiveUser: [id],
    }));
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={colUserInRole}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(role) || !role.users.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={colUserInRole.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      role.users,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const user = _.cloneDeep(row);
      return (
        <>
        <TableRow key={row.id}>
          {colUserInRole.map(column => {
            const value = user[column.id];
            if (column.id === 'no') {
              return <TableCell key={column.id}>{idx + 1}</TableCell>;
            }
            if (column.id === 'is_active') {
              return (
                <TableCell key={column.id}>
                  <Tooltip title="" enterDelay={500} leaveDelay={200}>
                    <i
                      style={{
                        color: value ? '#4caf50' : '#ff0000',
                        fontSize: '16px',
                      }}
                      className={`fa fa-${value ? 'check' : 'times'}-circle`}
                      aria-hidden="true"
                    ></i>
                  </Tooltip>
                </TableCell>
              );
            }
            if (column.id === 'actions') {
              return (
                <TableCell key={column.id}>
                   <Tooltip title={'Xo'} enterDelay={500} leaveDelay={100} onClick={() => {
                     deleteUserInRole(user.id);
                     setSearch(false);
                   }}>
                    <button
                      className="btn btn-danger btn-xs mx-2"
                    >
                      <i className="fa fa-trash-o"></i>
                    </button>
                  </Tooltip>
                </TableCell>
              );
            }
            return (
              <TableCell key={column.id}>{value ? value : '-'}</TableCell>
            );
          })}
        </TableRow>
        </>
      );
    });
  };

  return (
    <Dialog
      fullWidth
      maxWidth={'md'}
      open={open}
      onClose={onClose}
      disableBackdropClick
    >
      <DialogContent>
        {search && (
          <ModalFindUser
            open={true}
            onClose={() => setSearch(false)}
          />
        )}
        <form onSubmit={(e: React.FormEvent) => e.preventDefault()}>
          <div className="row">
            <div className="col-12 d-flex justify-content-between pb-3">
              <h4 className="d-block pb-2">Danh Sch Ngi Dng Quyn - { role ? role.description : '' }</h4>
            </div>
            <div className="col-sm-12 p-0">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
              </Paper>
            </div>
            <div className="col-sm-12 pt-2 d-flex justify-content-end">
              <button type="button" className="btn btn-secondary" onClick={onClose}>ng</button>
              <button type="button" className="btn btn-primary ml-3" onClick={() => setSearch(true)}>Thm ngi dng</button>
            </div>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
