/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState } from 'react';
import * as _ from 'lodash';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  root: {
    paddingBottom: 0,
    '& h2': {
      fontSize: '1.3rem !important',
      fontWeight: 'bold',
    },
  },
});

interface ModalProps {
  id: string;
  title: string;
  open: boolean;
  onClose: () => void;
  confirmMethod: (id?: string) => any;
  rejectMethod?: (arg: any) => any;
}

export const ConfirmDeleteRole = ({
  id,
  title,
  open,
  onClose,
  confirmMethod,
}: ModalProps) => {
  const classes = useStyles();

  const onOK = () => {
    confirmMethod(id);
    onClose();
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      disableBackdropClick={true}
    >
      <DialogTitle classes={classes}>{'Thng bo xc nhn'}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {title}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={onClose}
          color="secondary"
          classes={{ root: 'btn-default btn-danger' }}
        >
          ng
        </Button>
        <Button
          onClick={onOK}
          color="primary"
          classes={{ root: 'btn-default btn-primary t-btn-primary' }}
        >
          Xa
        </Button>
      </DialogActions>
    </Dialog>
  );
};
