/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */

import React, { useEffect, useState } from 'react';
import * as AuthSlice from 'store/auth/shared/slice';
import * as _ from 'lodash';
import * as AuthSelectors from 'store/auth/shared/selectors';
import { Dialog, DialogContent } from '@material-ui/core';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import { AuthSaga } from 'store/auth/shared/saga';
import {
  DEFAULT_FORMAT_INPUT_DATE,
  DEFAULT_ID,
} from 'store/common/constants/common.constant';
import { configResponse } from 'utils/request';
import { AuthHttp } from 'store/auth/services/auth.http';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';

enum FormKey {
  USER = 'userName',
  CODE = 'code',
  NAME = 'fullName',
  SEX = 'sex',
  BITRH = 'birthdate',
  EMAIL = 'email',
  PHONE = 'phone',
  POS = 'position',
  TITLE = 'title',
  ACTIVE = 'isActive',
}

export function ModalHRM({ open, onClose }) {
  const personalHttp = new AuthHttp();
  const dateUtil = new DateFnsUtils();
  const newDate = dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE);
  useInjectReducer({ key: AuthSlice.sliceKey, reducer: AuthSlice.reducer });
  useInjectSaga({ key: AuthSlice.sliceKey, saga: AuthSaga });
  const positions = useSelector(AuthSelectors.selectPositions);
  const titles = useSelector(AuthSelectors.selectTitles);
  const initUser = useSelector(AuthSelectors.selectUserFind);
  const loading = useSelector(AuthSelectors.selectLoading);
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    errors,
    setError,
    clearErrors,
    setValue,
  } = useForm();
  const [birthDay, setBirthDay] = React.useState<any>(newDate);
  const [errPhone, setErrPhone] = React.useState<boolean>(false);

  useEffect(() => {
    return () => {
      dispatch(AuthSlice.actions.clearData());
    };
  }, []);

  useEffect(() => {
    function initFormValue(user) {
      if (_.isEmpty(user)) return;
      setValue(FormKey.USER, user.user_name);
      setValue(FormKey.CODE, user.code);
      setValue(FormKey.NAME, user.full_name);
      setValue(FormKey.SEX, user.sex);
      setBirthDay(dateUtil.format(new Date(user.birthdate), 'yyyy-MM-dd'));
      setValue(FormKey.EMAIL, user.email);
      setValue(FormKey.PHONE, user.phone);
      setValue(FormKey.POS, user.position);
      setValue(FormKey.TITLE, user.title);
      setValue(FormKey.ACTIVE, user.is_active);
    }
    if (!_.isEmpty(initUser)) {
      setTimeout(() => initFormValue(initUser));
    }
  }, [initUser]);

  const onSubmit = async ({ code, userName, ...values }: any) => {
    try {
      let isCheckCode: any = {};
      let isCheckName: any = {};
      if (
        _.isEmpty(initUser) ||
        (!_.isEmpty(initUser) && code !== initUser.code)
      ) {
        const checkCodeRes = await personalHttp.checkCodePersonnel({
          id: DEFAULT_ID,
          code,
        });
        isCheckCode = await configResponse(checkCodeRes);
      }
      if (
        _.isEmpty(initUser) ||
        (!_.isEmpty(initUser) && userName !== initUser.user_name)
      ) {
        const checkNameRes = await personalHttp.checkUserNamePersonnel({
          id: DEFAULT_ID,
          userName,
        });
        isCheckName = await configResponse(checkNameRes);
      }
      if (_.isBoolean(isCheckName)) {
        setError(FormKey.USER, {
          type: 'exits',
          message: 'Tn ng nhp  tn ti',
        });
      }
      if (_.isBoolean(isCheckCode)) {
        setError(FormKey.CODE, {
          type: 'exits',
          message: 'M nhn vin  tn ti',
        });
      }
      if (!_.isBoolean(isCheckName) && !_.isBoolean(isCheckCode)) {
        if (_.isEmpty(initUser)) {
          dispatch(
            AuthSlice.actions.createPersonnel({
              ...values,
              birthdate: birthDay,
              code,
              userName,
            }),
          );
        } else {
          dispatch(
            AuthSlice.actions.updatePersonnel({
              ...values,
              code,
              userName,
              birthdate: birthDay,
              id: initUser.id,
            }),
          );
        }
      }
    } catch (err) {}
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      disableBackdropClick={true}
      scroll={'body'}
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <DialogContent>
        <div className="modal_personnel">
          <div className="personnel__title">
            <h3>TO NHN VIN</h3>
          </div>
          <div className="personnel__container">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="row">
                <div className="col-md-12 form-group">
                  <label htmlFor="">Tn ng nhp</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Tn ng nhp"
                    name={FormKey.USER}
                    ref={register({ required: true })}
                    onChange={() => clearErrors(FormKey.USER)}
                  />
                  {errors[FormKey.USER] &&
                    errors[FormKey.USER]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp tn ng nhp
                      </span>
                    )}
                  {errors[FormKey.USER] &&
                    errors[FormKey.USER]?.type === 'exits' &&
                    errors[FormKey.USER].message && (
                      <span style={{ color: '#fb0606' }}>
                        {errors[FormKey.USER].message}
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">M nhn vin</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="M nhn vin"
                    name={FormKey.CODE}
                    ref={register({ required: true })}
                  />
                  {errors[FormKey.CODE] &&
                    errors[FormKey.CODE]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp m nhn vin
                      </span>
                    )}
                  {errors[FormKey.CODE] &&
                    errors[FormKey.CODE]?.type === 'exits' && (
                      <span style={{ color: '#fb0606' }}>
                        {errors[FormKey.CODE].message}
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">H v tn</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="H v tn"
                    name={FormKey.NAME}
                    ref={register({
                      required: true,
                      maxLength: 50,
                    })}
                  />
                  {errors[FormKey.NAME] &&
                    errors[FormKey.NAME]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp h v tn nhn vin
                      </span>
                    )}
                  {errors[FormKey.NAME] &&
                    errors[FormKey.NAME]?.type === 'pattern' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng khng nhp s
                      </span>
                    )}
                  {errors[FormKey.NAME] &&
                    errors[FormKey.NAME]?.type === 'maxLength' && (
                      <span style={{ color: '#fb0606' }}>
                         di khng qu 50 k t
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label>Gii tnh</label>
                  <select
                    className="form-control"
                    name={FormKey.SEX}
                    ref={register({ required: true })}
                  >
                    <option value={1}>Nam</option>
                    <option value={0}>N</option>
                  </select>
                  {errors[FormKey.SEX] &&
                    errors[FormKey.SEX]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng chn gii tnh
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">Ngy sinh</label>
                  <input
                    type="date"
                    className="form-control"
                    name={FormKey.BITRH}
                    placeholder="Ngy sinh"
                    value={birthDay}
                    max={newDate}
                    onChange={e => setBirthDay(e.target.value)}
                  />
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">Email</label>
                  <input
                    name={FormKey.EMAIL}
                    type="email"
                    className="form-control"
                    placeholder="Email"
                    ref={register({ required: true })}
                  />
                  {errors[FormKey.EMAIL] &&
                    errors[FormKey.EMAIL]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp email nhn vin
                      </span>
                    )}
                  {errors[FormKey.EMAIL] &&
                    errors[FormKey.EMAIL]?.type === 'pattern' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp ng nh dng email
                      </span>
                    )}
                  {errors[FormKey.EMAIL] &&
                    errors[FormKey.EMAIL]?.type === 'maxLength' && (
                      <span style={{ color: '#fb0606' }}>
                         di email khng qu 50 k t
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">S in thoi</label>
                  <input
                    type="number"
                    className="form-control"
                    placeholder="S in thoi"
                    name={FormKey.PHONE}
                    ref={register({
                      required: true,
                      validate: phone => {
                        if (!phone || phone.length !== 10) return false;
                        return new RegExp(/^[0-9]+$/).test(phone);
                      },
                    })}
                  />
                  {errors[FormKey.PHONE] &&
                    errors[FormKey.PHONE]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp s in thoi
                      </span>
                    )}
                  {errors[FormKey.PHONE] &&
                    errors[FormKey.PHONE]?.type === 'validate' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng nhp  10 s
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">Chc danh</label>
                  <select
                    name={FormKey.TITLE}
                    className="form-control"
                    ref={register({ required: true })}
                  >
                    <option value="">Vui lng chn...</option>
                    {!_.isEmpty(titles) &&
                      titles.map((opt, index) => (
                        <option value={opt.id} key={index}>
                          {opt.description}
                        </option>
                      ))}
                  </select>
                  {errors[FormKey.TITLE] &&
                    errors[FormKey.TITLE]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng chn chc danh
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <label htmlFor="">Chc v</label>
                  <select
                    name={FormKey.POS}
                    className="form-control"
                    ref={register({ required: true })}
                  >
                    <option value="">Vui lng chn...</option>
                    {!_.isEmpty(positions) &&
                      positions.map((opt, index) => (
                        <option value={opt.id} key={index}>
                          {opt.description}
                        </option>
                      ))}
                  </select>
                  {errors[FormKey.POS] &&
                    errors[FormKey.POS]?.type === 'required' && (
                      <span style={{ color: '#fb0606' }}>
                        Vui lng chn chc v
                      </span>
                    )}
                </div>
                <div className="col-md-12 form-group">
                  <input
                    className="form-check-input ml-0 mt-2"
                    type="checkbox"
                    name={FormKey.ACTIVE}
                    id="active"
                    ref={register()}
                    defaultChecked
                  />
                  <label
                    className="form-check-label"
                    htmlFor="active"
                    style={{ paddingLeft: '20px' }}
                  >
                    Hot dng
                  </label>
                </div>
                <div className="form-group col-md-12 d-flex justify-content-end">
                  <input
                    type="button"
                    onClick={onClose}
                    className="btn btn-secondary"
                    value="ng"
                  />
                  <input
                    type="submit"
                    className="btn btn-primary ml-3"
                    value="Lu thay i"
                  />
                </div>
              </div>
            </form>
          </div>
          {loading ? <AppLoading loading={true} /> : null}
        </div>
      </DialogContent>
    </Dialog>
  );
}
