/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import * as _ from 'lodash';
import * as AuthSlice from 'store/auth/shared/slice';
import * as AuthSelector from 'store/auth/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { AuthSaga } from 'store/auth/shared/saga';
import { AppHelper, Order } from 'utils/app.helper';
import { Tooltip } from '@material-ui/core';
import {
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useStyles } from './ModalUserInRole';
import { useForm } from 'react-hook-form';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortCheck, { BlueCheckbox } from 'app/components/TableSortCheck';
import TablePagination from '@material-ui/core/TablePagination';
import styled from 'styled-components';

const ButtonSearch = styled.button`
  color: red;
  background: #fff;
  height: 37px;
  border: #ccc;
`;

export const colUserInRole: any[] = [
  { id: 'user_name', label: 'Tn ng nhp', minWidth: 120 },
  { id: 'code', label: 'M nhn vin', minWidth: 120 },
  { id: 'full_name', label: 'Tn nhn vin', minWidth: 120 },
  { id: 'is_active', label: 'Trng thi', minWidth: 120 },
];

export function ModalFindUser({ open, onClose }) {
  useInjectReducer({ key: AuthSlice.sliceKey, reducer: AuthSlice.reducer });
  useInjectSaga({ key: AuthSlice.sliceKey, saga: AuthSaga });
  const users = useSelector(AuthSelector.selectUsers);
  const role = useSelector(AuthSelector.selectRoleEditing);
  const dispatch = useDispatch();
  const classes = useStyles();
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('');
  const [selected, setSelected] = useState<string[]>([]);
  const { handleSubmit, register, getValues } = useForm();
  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  useEffect(() => {
    dispatch(AuthSlice.actions.getListUser({}));
    return () => {
      dispatch(AuthSlice.actions.clearUsers());
    };
  }, []);

  useEffect(() => {
    setSelected([]);
  }, [users]);

  const handleClick = (event: React.MouseEvent<unknown>, name: string) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected: string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    setSelected(newSelected);
  };

  const renderSearch = () => {
    return (
      <form
        className="btn-group d-flex align-items-center"
        onSubmit={handleSubmit(submitSearch)}
      >
        <label className="m-0">
          <input
            type="search"
            placeholder="Tm Ngi Dng"
            className="form-control form-control-sm"
            aria-controls="titleTable"
            ref={register()}
            name="fullName"
          />
        </label>
        <ButtonSearch className="btn btn-info m-0 ml-2" type="submit">
          <i className="fa fa-search" />
        </ButtonSearch>
      </form>
    );
  };

  const submitSearch = ({ fullName }) => {
    dispatch(
      AuthSlice.actions.getListUser({
        fullName,
        pageNumber: 1,
        pageSize: 10,
      }),
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked && !_.isEmpty(users) && users.data.length) {
      const newSelecteds = users.data.map((n: any) => n.id);
      return setSelected(newSelecteds);
    }
    setSelected([]);
  };

  const renderColumns = () => {
    return (
      <TableSortCheck
        cols={colUserInRole}
        numSelected={selected.length}
        order={order}
        orderBy={orderBy}
        onSelectAllClick={handleSelectAllClick}
        onRequestSort={handleRequestSort}
        rowCount={
          _.isEmpty(users) || !users.data.length ? 10 : users.data.length
        }
      />
    );
  };

  const handleChangePage = (event: unknown, pageNumber: number) => {
    let newPagi = {
      pageSize: users.page_size,
      pageNumber: pageNumber + 1,
      search: getValues('fullName'),
    };
    dispatch(AuthSlice.actions.getListUser(newPagi));
  };

  const handleChangeRowsPerPage = event => {
    let newPagi = {
      pageSize: +event.target.value,
      pageNumber: 1,
      fullName: getValues('fullName'),
    };
    dispatch(AuthSlice.actions.getListUser(newPagi));
  };

  const renderPagination = () => {
    if (!_.isEmpty(users) && users.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin Th"
          count={users.total_records}
          page={users?.page_number - 1}
          rowsPerPage={users?.page_size}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  const renderTable = () => {
    if (_.isEmpty(users) || !users.data.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={colUserInRole.length + 1}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      users.data,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const user = _.cloneDeep(row);
      const isItemSelected = isSelected(user.id);
      return (
        <>
          <TableRow
            key={row.id}
            hover
            onClick={event => handleClick(event, user.id)}
            role="checkbox"
            aria-checked={isItemSelected}
            tabIndex={-1}
            selected={isItemSelected}
          >
            <TableCell padding="checkbox" align="center">
              <BlueCheckbox
                checked={isItemSelected}
                inputProps={{ 'aria-labelledby': 'id' }}
              />
            </TableCell>
            {colUserInRole.map(column => {
              const value = user[column.id];
              if (column.id === 'is_active') {
                return (
                  <TableCell key={column.id} align="center">
                    <Tooltip title="" enterDelay={500} leaveDelay={200}>
                      <i
                        style={{
                          color: value ? '#4caf50' : '#ff0000',
                          fontSize: '16px',
                        }}
                        className={`fa fa-${value ? 'check' : 'times'}-circle`}
                        aria-hidden="true"
                      ></i>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={column.id}>{value ? value : '-'}</TableCell>
              );
            })}
          </TableRow>
        </>
      );
    });
  };

  const addUserToRole = async () => {
    if (!selected.length) {
      return console.log('Error');
    }
    const currentUser = role.users;
    if (!currentUser || !currentUser.length) {
      await dispatch(
        AuthSlice.actions.addUserInRole({
          id: role.id,
          activeUser: selected,
          inActiveUser: [],
        }),
      );
    } else {
      await dispatch(
        AuthSlice.actions.addUserInRole({
          id: role.id,
          activeUser: [...selected, ...currentUser.map(({ id }) => id)],
          inActiveUser: [],
        }),
      );
    }
    onClose();
  };

  return (
    <Dialog
      fullWidth
      maxWidth={'md'}
      open={open}
      onClose={onClose}
      disableBackdropClick
    >
      <DialogContent>
        <div>
          <div className="row">
            <div className="col-12 d-flex justify-content-between pb-3">
              <h4 className="d-block pb-2">Danh Sch Ngi Dng</h4>
              {renderSearch()}
            </div>
            <div className="col-sm-12 p-0">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
            <div className="col-sm-12 pt-2 d-flex justify-content-end mt-3">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={onClose}
              >
                ng
              </button>
              <button
                type="button"
                className="btn btn-primary ml-3"
                onClick={addUserToRole}
              >
                Thm vo quyn {role.name}
              </button>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
